import React, { useState, useEffect, useCallback } from 'react'
import { EmblaOptions } from 'embla-carousel/vanilla/components/options'
import { useEmblaCarousel } from 'embla-carousel/react'
import { PrevButton, NextButton } from './NavButton'
import { Scene, SceneContainer } from './Scene'
import { DotWrap, DotButton } from './Dots'
import { Slide } from './Slide'

type PropType = {
  slides: number[]
  showNavigation?: boolean
  options?: EmblaOptions
}

const Carousel = (props: PropType) => {
  const { slides, showNavigation = false, options = {} } = props
  const [EmblaCarousel, embla] = useEmblaCarousel({
    ...options,
    selectedClass: '',
    draggableClass: '',
    draggingClass: '',
  })
  const [prevBtnEnabled, setPrevBtnEnabled] = useState(false)
  const [nextBtnEnabled, setNextBtnEnabled] = useState(false)
  const [selectedIndex, setSelectedIndex] = useState(0)
  const [scrollSnaps, setScrollSnaps] = useState<number[]>([])

  const scrollTo = useCallback(
    (index: number) => embla && embla.scrollTo(index),
    [embla],
  )
  const scrollPrev = useCallback(() => embla && embla.scrollPrev(), [embla])
  const scrollNext = useCallback(() => embla && embla.scrollNext(), [embla])

  const onSelect = useCallback(() => {
    if (!embla) return
    setSelectedIndex(embla.selectedScrollSnap())
    setPrevBtnEnabled(embla.canScrollPrev())
    setNextBtnEnabled(embla.canScrollNext())
  }, [embla])

  useEffect(() => {
    if (!embla) return
    setScrollSnaps(embla.scrollSnapList())
    embla.on('select', onSelect)
    onSelect()
  }, [embla, onSelect])

  return (
    <>
      <Scene>
        <EmblaCarousel>
          <SceneContainer axis={options.axis || 'x'}>
            {slides.map((width, index) => (
              <Slide key={index} width={width}>
                {index + 1}
              </Slide>
            ))}
          </SceneContainer>
        </EmblaCarousel>
        {showNavigation && (
          <>
            <PrevButton onClick={scrollPrev} enabled={prevBtnEnabled} />
            <NextButton onClick={scrollNext} enabled={nextBtnEnabled} />
          </>
        )}
      </Scene>
      {showNavigation && (
        <DotWrap>
          {scrollSnaps.map((_, index) => (
            <DotButton
              key={index}
              selected={index === selectedIndex}
              onClick={() => scrollTo(index)}
            />
          ))}
        </DotWrap>
      )}
    </>
  )
}

export default Carousel
