import React, { PropsWithChildren } from 'react'
import { useTabAccess, tabAccessStyles } from '../TabAccess'
import styled, { css } from 'styled-components'
import { plainButtonStyles } from './ButtonStyles'

const Wrap = styled.button`
  ${plainButtonStyles}
`

export type PropType = PropsWithChildren<
  React.ButtonHTMLAttributes<HTMLButtonElement>
>

const Button = (props: PropsWithChildren<PropType>) => {
  const tabAccess = useTabAccess()
  const { children, ...restProps } = props

  return (
    <Wrap
      {...restProps}
      css={css`
        ${tabAccess ? tabAccessStyles : ''}
      `}
    >
      {children}
    </Wrap>
  )
}

export default Button
