import { media, transition, color } from '../Settings'
import { css } from 'styled-components'

type PropType = {
  defaultColor?: string
  hoverColor?: string
  defaultBackgroundColor?: string
  hoverBackgroundColor?: string
}

export const plainButtonStyles = css`
  display: inline-flex;
  border: 0;
  outline: 0;
  padding: 0;
  margin: 0;
  background-color: transparent;
  font-weight: bold;
  text-decoration: none;
  cursor: pointer;
`

export const brandOutlineStyles = css<PropType>`
  background-color: 'transparent';
  box-shadow: 0 0 0 1px ${color.brand};
  color: ${color.brand};
  border-radius: 5rem;
  padding: 1rem 3rem;

  ${media.minSm} {
    border-radius: 4rem;
    padding: 1.2rem 3.6rem;
  }
  ${media.minMd} {
    padding: 1.4rem 3.8rem;
  }
  ${media.minLg} {
    transition: color ${transition.default};
    &:hover {
      color: ${color.prague};
    }
  }
`
