import React, { useEffect, useCallback } from 'react'
import { EmblaOptions } from 'embla-carousel/vanilla/components/options'
import { useEmblaCarousel } from 'embla-carousel/react'
import { Slide, ThumbnailSlide } from '../Default/Slide'
import {
  Scene,
  SceneContainer,
  ThumbnailScene,
  ThumbnailSceneContainer,
} from '../Default/Scene'

type PropType = {
  slides: number[]
  options?: EmblaOptions
}

const Carousel = (props: PropType) => {
  const { slides, options = {} } = props
  const [EmblaCarousel, embla] = useEmblaCarousel({
    ...options,
    selectedClass: '',
    draggableClass: '',
    draggingClass: '',
  })
  const [EmblaCarouselThumbs, emblaThumbs] = useEmblaCarousel({
    containScroll: 'keepSnaps',
    dragFree: false,
    selectedClass: '',
    draggableClass: '',
    draggingClass: '',
  })

  const onThumbClick = useCallback(
    (index: number) => {
      if (!embla || !emblaThumbs) return
      if (emblaThumbs.clickAllowed()) {
        embla.scrollTo(index)
      }
    },
    [embla, emblaThumbs],
  )

  const onSelect = useCallback(() => {
    if (!embla || !emblaThumbs) return
    emblaThumbs.scrollTo(embla.selectedScrollSnap())
  }, [embla, emblaThumbs])

  useEffect(() => {
    if (!embla) return
    embla.on('select', onSelect)
    onSelect()
  }, [embla, onSelect])

  return (
    <>
      <Scene>
        <EmblaCarousel>
          <SceneContainer axis="x">
            {slides.map((width, index) => (
              <Slide key={index} width={width}>
                {index + 1}
              </Slide>
            ))}
          </SceneContainer>
        </EmblaCarousel>
        <ThumbnailScene>
          <EmblaCarouselThumbs>
            <ThumbnailSceneContainer axis={options.axis || 'x'}>
              {slides.map((_, index) => (
                <ThumbnailSlide
                  onClick={() => onThumbClick(index)}
                  key={index}
                  width={20}
                >
                  {index + 1}
                </ThumbnailSlide>
              ))}
            </ThumbnailSceneContainer>
          </EmblaCarouselThumbs>
        </ThumbnailScene>
      </Scene>
    </>
  )
}

export default Carousel
