import React, { PropsWithChildren } from 'react'
import styled from 'styled-components'
import { color, brandGradient, media } from '../../Settings'
import { H1Style, H2Style } from '../../Text'

const Wrap = styled.div<PropType>`
  ${H1Style}
  min-width: ${({ width }) => `${width}%`};
  position: relative;
  height: 100%;
  padding-left: 0.6rem;
  padding-top: 0.6rem;

  ${media.minSm} {
    padding-left: 0.8rem;
    padding-top: 0.8rem;
  }
  ${media.minMd} {
    padding-left: 1rem;
    padding-top: 1rem;
  }
`

const Inner = styled.div`
  ${brandGradient};
  color: ${color.prague};
  display: flex;
  align-items: center;
  text-align: center;
  justify-content: center;
  height: 100%;
`

type PropType = React.HTMLAttributes<HTMLDivElement> & { width: number }

export const Slide = (props: PropsWithChildren<PropType>) => {
  const { width, children, ...restProps } = props

  return (
    <Wrap width={width} {...restProps}>
      <Inner>{children}</Inner>
    </Wrap>
  )
}

export const ThumbnailSlide = styled(Slide)`
  ${H2Style}
  padding-left: 0.3rem;
  cursor: pointer;

  ${media.minSm} {
    padding-left: 0.6rem;
  }
  ${media.minMd} {
    padding-left: 0.8rem;
  }
`
