import React from 'react'
import styled from 'styled-components'
import { H1, H2, H3Style, Bold, H3 } from '../Text'
import { ButtonLinkBrandOutline } from '../Button/ButtonBrand'
import { Grid, Section } from '../PageLayout'
import CodeBlock from '../CodeBlock'
import { Hero } from '../Hero'
import { H4 } from '../Text/H4'
import { media, color } from '../Settings'
import { createSquareSizeStyles } from '../utils'
import Svg from '../Svg'

const HeroIcon = styled.div`
  display: inline-block;
  ${createSquareSizeStyles('8rem')}

  ${media.minSm} {
    ${createSquareSizeStyles('9.5rem')}
  }
  ${media.minMd} {
    ${createSquareSizeStyles('10rem')}
  }
`

const IntroText = styled.h2`
  ${H3Style}
  font-weight: 200;
  margin-bottom: 1.5rem;

  ${media.minSm} {
    margin-bottom: 2rem;
  }
  ${media.minMd} {
    margin-bottom: 2.2rem;
  }
`

const Home = () => {
  return (
    <>
      <Hero>
        <HeroIcon>
          <Svg icon="emblaLogo" color={color.brand} />
        </HeroIcon>
        <H1>Embla Carousel</H1>
        <IntroText>
          Extensible bare bones carousels for the web, written in TypeScript
        </IntroText>
        <ButtonLinkBrandOutline smooth to="/#introduction">
          Get Started
        </ButtonLinkBrandOutline>
      </Hero>
      <Grid>
        <Section>
          <H2 id="introduction">Introduction</H2>
          <p>
            <Bold>embla-carousel</Bold> is a bare bones carousel library with
            great fluid motion and awesome touch/swipe precision. It's library
            agnostic, <Bold>dependency free</Bold> and 100% open source. Build
            awesome carousels by extending them with your own CSS and
            JavaScript. It works in{' '}
            <Bold>all modern browsers including IE 11</Bold>.
          </p>
        </Section>
        <Section>
          <H3 id="installation">Installation</H3>
          <p>
            <Bold>embla-carousel</Bold> can easily be installed in every
            JavaScript or TypeScript project built for the web, either using a
            package manager or a CDN.
          </p>
          <H4 id="module-usage">Module usage</H4>
          <CodeBlock language="bash">
            npm install embla-carousel --save
          </CodeBlock>
          <CodeBlock language="javascript">
            {`import EmblaCarousel from 'embla-carousel'
const emblaNode = document.getElementById('embla')

const embla = EmblaCarousel(emblaNode)`}
          </CodeBlock>
          <H4>The HTML</H4>
          <CodeBlock language="html">
            {`<div class="embla" id="embla">
  <div class="embla__container">
    <div class="embla__slide">Slide 1</div>
    <div class="embla__slide">Slide 2</div>
    <div class="embla__slide">Slide 3</div>
  </div>
</div>`}
          </CodeBlock>
          <H4>The CSS</H4>
          <CodeBlock language="css">
            {`.embla {
  overflow: hidden;
}
.embla__container {
  display: flex;
}
.embla__slide {
  position: relative;
  flex: 0 0 100%;
}`}
          </CodeBlock>

          <H4 id="react-usage">React usage</H4>
          <CodeBlock language="jsx">
            {`import React, { useEffect } from 'react'
import { useEmblaCarousel } from 'embla-carousel/react'

const EmblaCarousel = () => {
  const [EmblaCarouselReact, embla] = useEmblaCarousel({ loop: false })

  useEffect(() => {
    if (!embla) return

    embla.on('select', () => {
      console.log(\`Index is \${embla.selectedScrollSnap()}\`)
    })
  }, [embla])

  return (
    <EmblaCarouselReact>
      <div style={{ display: 'flex' }}>
        <div style={{ minWidth: '100%' }}>1</div>
        <div style={{ minWidth: '100%' }}>2</div>
        <div style={{ minWidth: '100%' }}>3</div>
      </div>
    </EmblaCarouselReact>
  )
}

export default EmblaCarousel`}
          </CodeBlock>
          <H4 id="cdn-usage">CDN as an alternative</H4>
          <CodeBlock language="html">
            {`<html>
  <head>
    <style>
      .embla {
        overflow: hidden;
      }
      .embla__container {
        display: flex;
      }
      .embla__slide {
        position: relative;
        flex: 0 0 100%;
      }
    </style>
  </head>
  <body>
    <div class="embla" id="embla">
      <div class="embla__container">
        <div class="embla__slide">1</div>
        <div class="embla__slide">2</div>
        <div class="embla__slide">3</div>
      </div>
    </div>
    <script src="https://cdn.jsdelivr.net/npm/embla-carousel@latest/embla-carousel.umd.js"></script>
    <script>
      var emblaNode = document.getElementById("embla");
      var embla = EmblaCarousel(emblaNode);
    </script>
  </body>
</html>`}
          </CodeBlock>
        </Section>
      </Grid>
    </>
  )
}

export default Home
